import React, {Component} from 'react'

class Product extends Component {
  constructor(props) {
    super(props);
    this.state = { qtde: 0 , sum: 0};

    this.handleChange = this.handleChange.bind(this);
  }

  get_total_price() {
    console.log("get_total_price")
    return (this.state.sum).toFixed(2)
  }


  handleChange(event) {
    console.log("handleChange")
    const target = event.target;
    let value = target.type === 'checkbox' ? target.checked : target.value;
    let qtde = this.state.qtde
    const name = target.name;
    console.log(name, value)

    if (name == "qtde" && value < 0){
      value = 0;
      qtde = 0}

    if (name == "qtde" ){
      this.sum = value * this.props.info.price;
    }

    this.setState({
      [name]: value,
      "sum": this.sum
    });

    this.props.onProductQuantityChange(this.props.info.name, {sum: this.sum, qtde: value})
  }

  render() {
    return (
        <tr>
            <td data-th="Product">
                <div class="row">
                    <div class="col-sm-2 hidden-xs" hidden><img src="http://placehold.it/100x100" alt="..." class="img-responsive"/></div>
                    <div class="col-sm-10">
                        <h4 class="nomargin">{this.props.info.name}</h4>
                    </div>
                </div>
            </td>
            <td data-th="Unidade" hidden>
                <select class="form-control" id="exampleFormControlSelect1">
                  <option>Ma√ßo</option>
                </select>
            </td>
            <td data-th="Price">R$ {(this.props.info.price).toFixed(2)}</td>
            <td data-th="Quantity">
              <input name="qtde" type="number" class="form-control text-center" min="0" value={this.state.qtde} onChange={this.handleChange}/>
            </td>
            <td data-th="Subtotal" class="text-center">R$ {this.get_total_price()}</td>
        </tr>
    );
  }
}

export default Product;
